/*

Atom One Light by Daniel Gamage
Original One Light Syntax theme from https://github.com/atom/one-light-syntax

base:    #fafafa
mono-1:  #383a42
mono-2:  #686b77
mono-3:  #a0a1a7
hue-1:   #0184bb
hue-2:   #4078f2
hue-3:   #a626a4
hue-4:   #50a14f
hue-5:   #e45649
hue-5-2: #c91243
hue-6:   #986801
hue-6-2: #c18401

*/

.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  color: #383a42;
  background: #fafafa;
}

.hljs-comment,
.hljs-quote {
  color: #a0a1a7;
  font-style: italic;
}

.hljs-doctag,
.hljs-keyword,
.hljs-formula {
  color: #a626a4;
}

.hljs-section,
.hljs-name,
.hljs-selector-tag,
.hljs-deletion,
.hljs-subst {
  color: #e45649;
}

.hljs-literal {
  color: #0184bb;
}

.hljs-string,
.hljs-regexp,
.hljs-addition,
.hljs-attribute,
.hljs-meta-string {
  color: #50a14f;
}

.hljs-built_in,
.hljs-class .hljs-title {
  color: #c18401;
}

.hljs-attr,
.hljs-variable,
.hljs-template-variable,
.hljs-type,
.hljs-selector-class,
.hljs-selector-attr,
.hljs-selector-pseudo,
.hljs-number {
  color: #986801;
}

.hljs-symbol,
.hljs-bullet,
.hljs-link,
.hljs-meta,
.hljs-selector-id,
.hljs-title {
  color: #4078f2;
}

.hljs-emphasis {
  font-style: italic;
}

.hljs-strong {
  font-weight: bold;
}

.hljs-link {
  text-decoration: underline;
}

table.o-doc{width:100%;margin:8px 0 16px;border:1px solid #ebedf0;line-height:1.5;font-size:.14rem;border-collapse:collapse;border-spacing:0;empty-cells:show
}
table.o-doc th,table.o-doc td{border:1px solid #ebedf0;padding:1em 1.5em;text-align:left
}
table.o-doc th{white-space:nowrap;color:#678;font-weight:500;background:rgba(0,0,0,0.02)
}
table.o-doc th .cn{font-size:12px;padding-left:.5em
}
table.o-doc td{color:#333;font-size:12px
}
table.o-doc td .code{margin:0 1px;background:#f2f4f5;padding:.2em .4em;border-radius:3px;font-size:12px;border:1px solid #eee
}
table.o-doc td:first-child{font-weight:500;width:20%;font-size:inherit
}
table.o-doc td:nth-child(3){width:22%
}
table.o-doc td:last-child{width:13%
}
